import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material';
import { AngularFireStorage } from 'angularfire2/storage';
import { Observable } from 'rxjs/Observable';
import { Subscription } from 'rxjs/Subscription';

import { ItemService } from '../../services/item.service';
import { Item } from '../../models/item';
import { DeleteItemComponent } from './delete-item.component';
// import { environment } from '../../../environments/environment.prod';

@Component({
  selector: 'app-items',
  templateUrl: './items.component.html',
  styleUrls: ['./items.component.css']
})
export class ItemsComponent implements OnInit, OnDestroy {
  httpsUrl: string;
  items: Item[];
  editState = false;
  itemToEdit: Item;
  myForm: FormGroup;
  loadingMsg = 'Loading ...  ';
  sortUp = false;
  @Input()user: any;
  itemsSubscription: Subscription;
  categories = ['JE (Civ)', 'JE (E/M)', 'JE(QS&C)', 'OS', 'AAO', 'UDC', 'SAA', 'LDC', 'JAA',
                                         'STENO GDE-III', 'STENO', 'DTMN', 'BS', 'SK', 'CHOW(O)'];
  constructor(private itemService: ItemService, private dialog: MatDialog,
                                                      private storageService: AngularFireStorage) {}

  ngOnInit() {
    this.itemsSubscription = this.itemService.getItems().subscribe(items => {
      this.items = items;
      this.itemService.count = this.items.length;
      this.loadingMsg = 'There are no records.';
      console.log('Items Count : ' + items.length);
      this.itemService.itemsSubject.next(this.items);
      }, error => console.log(error)
    );
    this.itemService.search.subscribe((item: Item) => {
      this.itemService.filterItems(item).subscribe(items => {
        this.items = items;
        this.itemService.count = this.items.length;
        this.itemService.itemsSubject.next(this.items);
      });
    });
    this.itemService.sort.subscribe((index: number) => {
      if (index) {
        const keys = ['MES_No', 'NAME', 'CATEGORY', 'DOB', 'DOJ', 'UNIT',
                      'UNIT_SENIORITY', 'STATION', 'STN_SENIORITY', 'REMARKS'];
        this.sortOnElement(keys[index]);
      } else {
        this.sortOnNumber();
      }
      this.itemService.itemsSubject.next(this.items);
    });
    this.itemService.loginStatus.subscribe(status => {
      this.itemsSubscription.unsubscribe();
      this.clearState();
    });
  }
  ngOnDestroy() {
    this.itemsSubscription.unsubscribe();
  }
  sortOnNumber() {
    this.sortUp = !this.sortUp;
    if (this.sortUp) {
        this.items.sort(function(a, b){return a['MES_No'] - b['MES_No']; });
    } else {
        this.items.sort(function(a, b){return b['MES_No'] - a['MES_No']; });
    }
  }
  sortOnElement(key: string) {
    this.sortUp = !this.sortUp;
    if (this.sortUp) {
        this.items.sort(function(a, b){
            const x = a[key];
            const y = b[key];
            if (x < y) {return -1; }
            if (x > y) {return 1; }
            return 0;
        });
    } else {
        this.items.sort(function(a, b){
            const x = a[key];
            const y = b[key];
            if (x < y) {return 1; }
            if (x > y) {return -1; }
            return 0;
        });
    }
  }
  deleteItem(item: Item) {
    const dialogRef = this.dialog.open(DeleteItemComponent, {
      hasBackdrop: true,
      // role: 'alertdialog'
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.clearState();
        this.itemService.deleteItem(item);
      }
    });
  }
  editItem(item: Item) {
    this.editState = true;
    this.itemToEdit = item;
    this.myForm = new FormGroup({
      mesno: new FormControl(item.MES_No, Validators.required),
      name: new FormControl(item.NAME, Validators.required),
      category: new FormControl(item.CATEGORY, Validators.required),
      dob: new FormControl(item.DOB, Validators.required),
      doj: new FormControl(item.DOJ, Validators.required),
      unit: new FormControl(item.UNIT, Validators.required),
      unit_seniority: new FormControl(item.UNIT_SENIORITY, Validators.required),
      station: new FormControl(item.STATION, Validators.required),
      stn_seniority: new FormControl(item.STN_SENIORITY, Validators.required),
      remarks: new FormControl(item.REMARKS)
    });
    console.log('item.id : ' + item.id);
  }
  updateItem() {
    console.log(this.myForm);
    const item = {
      MES_No: +this.myForm.value.mesno,
      NAME: this.myForm.value.name,
      CATEGORY: this.myForm.value.category,
      DOB: this.getDateStr(this.myForm.value.dob),
      DOJ: this.getDateStr(this.myForm.value.doj),
      UNIT: this.myForm.value.unit,
      UNIT_SENIORITY: this.getDateStr(this.myForm.value.unit_seniority),
      STATION: this.myForm.value.station,
      STN_SENIORITY: this.getDateStr(this.myForm.value.stn_seniority),
      REMARKS: this.myForm.value.remarks,
      id: this.itemToEdit.id
    };
    this.itemService.updateItem(item);
    this.clearState();
  }
  clearState() {
    console.log('State Cleared');
    this.editState = false;
    this.itemToEdit = null;
  }
  getDateStr(dtStr: string) {
    if (dtStr) {
      const dt = new Date(dtStr);
      const month = dt.getMonth() + 1;
      const monthStr = month < 10 ? '0' + month : '' + month;
      const date = dt.getDate();
      const dateStr = date < 10 ? '0' + date : '' + date;
      return dt.getFullYear() + '-' + monthStr + '-' + dateStr;
    } else {
      return dtStr;
    }
  }
  showSB(item: Item) {
    if (this.user && (this.user.email || item.MES_No === +this.user.uid)) {
      const storageRef = this.storageService.storage.ref();
      storageRef.child(item.MES_No + '.pdf').getDownloadURL().then(function(url) {
        window.open(url);
      });
    }
  }
  showPointer(item: Item) {
    if (this.user && (this.user.email || item.MES_No === +this.user.uid)) {
      return true;
    } else {
      return false;
    }
  }
}



