
import { Component } from '@angular/core';
import { NgForm } from '@angular/forms';
import { AngularFireAuth } from 'angularfire2/auth';
import { Http, Response , Headers} from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent {
  mesNo = '';
  password = '';
  loginFailedMessage = 'On your first Login with MES No, any password you enter would be registered for your subsequent Login';
  signinImagePath = 'https://cecc-staff.github.io/assets/signinImage.jpg';

  constructor(private afAuth: AngularFireAuth, private http: Http) {}

  onSubmit(form: NgForm) {
    const pattern = new RegExp('[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,3}$');
    if (pattern.test(this.mesNo)) {
      this.afAuth.auth.signInWithEmailAndPassword(this.mesNo, this.password)
        .then(result => {console.log(result); })
        .catch(error => {
            console.log(error);
            this.loginFailedMessage = 'Invalid Email Id or Password';
        });
    } else {
      const headers = new Headers();
      headers.append('Content-Type', 'application/json');
      const user = { MES_No: this.mesNo, Password: this.password };
      this.http.post('https://us-central1-cecc-staff.cloudfunctions.net/uploadForm2',
                                              JSON.stringify(user), {headers: headers})
              .map((response: Response) => response.json())
              .catch((error: Response) => Observable.throw(error.json()))
              // tslint:disable-next-line:no-shadowed-variable
              .subscribe(result => {console.log(result);
                if (result.token) {
                    this.afAuth.auth.signInWithCustomToken(result.token).catch(function(error) {
                      console.log('ErrorCode: ' + error.code + ' Message: ' + error.message);
                    });
                }
                if (result.Error) {
                    this.loginFailedMessage = result.Error;
                }
      });
    }
  }
}



