import { Component, OnInit, OnDestroy, Input, Output, EventEmitter } from '@angular/core';
import { PTOService } from '../services/pto.service';
import { Subscription } from 'rxjs/Subscription';
import jsPDF from 'jspdf/dist/jspdf.min';

import { Casuality, CasualityPTO } from '../models/casuality';
import { PTO } from '../models/pto';

@Component({
  selector: 'app-cas-list',
  templateUrl: './cas-list.component.html',
  styleUrls: ['./cas-list.component.css']
})
export class CasListComponent implements OnInit, OnDestroy {
  casSubscription: Subscription;
  ptos: PTO[];
  casualities: CasualityPTO[] = [];
  mesno: string;
  dateFrom: string;
  dateUpto: string;
  name: string;
  category: string;
  sortCol: number;
  sortUp: boolean;
  @Output() detailClicked = new EventEmitter<void>();

  constructor(private ptoService: PTOService) {}

  ngOnInit() {
    console.log('Cas ngOnInit Called!');
    this.casSubscription = this.ptoService.getPtos().subscribe(ptos => {
      console.log('Nos. of ptos : ', ptos.length);
      this.ptos = ptos;
      this.casSummarySubscribed();
    });
  }
  ngOnDestroy() {
    this.casSubscription.unsubscribe();
  }
  casSummarySubscribed() {
    this.ptoService.casSummary.subscribe((obj: {mesno: string, from: string, upto: string}) => {
      this.casualities = [];
      console.log('casSummary subscribed');
      this.mesno = obj.mesno;
      this.name = 'Not Found';
      this.category = '';
      this.dateFrom = obj.from;
      this.dateUpto = obj.upto;
      for (const pto of this.ptos) {
        for (const cas of pto.CASUALITIES) {
          if (+cas.MES_No === +obj.mesno) {
            const cas_pto: CasualityPTO = {
              ...cas,
              PTO_No: pto.PTO_No,
              PTO_YEAR: pto.YEAR,
              PTO_UNIT: pto.UNIT,
              PTO_STATION: pto.STATION,
              PTO_DATE: pto.DATE
            };
            this.casualities.push(cas_pto);
            this.name = cas_pto.NAME;
            this.category = cas_pto.CATEGORY;
            // console.log('Date From/Upto : ', pto.DATE + ' ' + this.dateFrom);
          }
        }
      }
      if (this.dateFrom) {
        this.casualities = this.casualities.filter(value => value.PTO_DATE >= this.dateFrom);
      }
      if (this.dateUpto) {
        this.casualities = this.casualities.filter(value => value.PTO_DATE <= this.dateUpto);
      }
    });
  }
  onSort(index: number) {
    this.sortCol = index;
    this.sortUp = !this.sortUp;
    const keys = ['PTO_No', 'PTO_DATE', 'PTO_UNIT', 'DATE', 'PARTICULAR'];
      if (index > 0) {
        this.sortOnElement(keys[index]);
      } else {
        this.sortOnPTONumber();
      }
  }
  sortOnPTONumber() {
    // this.sortUp = !this.sortUp;
    if (this.sortUp) {
        this.casualities.sort(function(a, b){
          const x = a['PTO_YEAR'];
          const y = b['PTO_YEAR'];
          if (x < y) {return -1; }
          if (x > y) {return 1; }
          if (x === y) {
            const p = a['PTO_No'];
            const q = b['PTO_No'];
            if (p < q) {return -1; }
            if (p > q) {return 1; }
            return 0;
          }
        });
    } else {
      this.casualities.sort(function(a, b){
        const x = a['PTO_YEAR'];
        const y = b['PTO_YEAR'];
        if (x < y) {return 1; }
        if (x > y) {return -1; }
        if (x === y) {
          const p = a['PTO_No'];
          const q = b['PTO_No'];
          if (p < q) {return 1; }
          if (p > q) {return -1; }
          return 0;
        }
      });
    }
  }
  sortOnElement(key: string) {
    // this.sortUp = !this.sortUp;
    if (this.sortUp) {
        this.casualities.sort(function(a, b){
            const x = a[key];
            const y = b[key];
            if (x < y) {return -1; }
            if (x > y) {return 1; }
            return 0;
        });
    } else {
        this.casualities.sort(function(a, b){
            const x = a[key];
            const y = b[key];
            if (x < y) {return 1; }
            if (x > y) {return -1; }
            return 0;
        });
    }
  }
  showDetails(cas: CasualityPTO) {
    this.detailClicked.emit();
    const pto: PTO = {
      PTO_No: cas.PTO_No,
      YEAR: cas.PTO_YEAR,
      UNIT: cas.PTO_UNIT,
      STATION: cas.PTO_STATION,
      DATE: cas.PTO_DATE,
      CASUALITIES: []
    };
    this.ptoService.filterPtos(pto).subscribe(ptos => {
      this.ptoService.pto = ptos[0];
    });
  }
  print() {
    jsPDF.API.hline = function(x, y, width){
      this.lines([[width, 0]], x, y, [1, 1], 'S', false);
    };

    jsPDF.API.heading = function(mesno, name, category, dateFrom, dateUpto){
      this.setFontSize(16);
      this.text(['Casualities of', 'MES No.: ' + mesno + ', ' + name + ', ' + category,
                 'From : ' + dateFrom + '   To : ' + dateUpto], 50, 20);
      this.setLineWidth(0.3);
      this.hline(15, 38, 180);
      this.hline(15, 55, 180);

      this.setFontSize(10);
      this.text('PTO No.', 15, 48);
      this.text('PTO Date', 35, 48);
      this.text(['Unit', 'Station'], 60, 48);
      this.text(['Casuality', 'Date'], 80, 48);
      this.text('Particulars', 140, 48);
    };

    const doc = new jsPDF();
    doc.heading(this.mesno, this.name, this.category, this.dateFrom, this.dateUpto);
    let partY = 60;
    for (const cas of this.casualities) {
      const arrPart = this.split(cas.PARTICULAR, 55);
      doc.text(cas.PTO_No + '/' + cas.PTO_YEAR, 15, partY);
      doc.text(cas.PTO_DATE, 35, partY);
      doc.text([cas.PTO_UNIT, cas.PTO_STATION], 60, partY);
      doc.text(cas.DATE, 80, partY);
      doc.text(arrPart, 105, partY);
      partY = arrPart.length < 3 ? partY + 15 : partY + arrPart.length * 5;
      if (partY > 260) {
        doc.addPage();
        doc.heading();
        partY = 60;
      }
    }
    doc.save(this.mesno + '_' + this.dateFrom + '_' + this.dateUpto + '.pdf');
  }

  split(str: string, segment: number) {
    let index = 0;
    const arrStr: string[] = [];
    let part: string;
    while (index < str.length) {
      part = str.substr(index, segment);
      arrStr.push(part);
      index += segment;
    }
    return arrStr;
  }
}



