import { Component } from '@angular/core';

import { ItemService } from '../../services/item.service';
import { Item } from '../../models/item';

@Component({
  selector: 'app-add-item',
  templateUrl: './add-item.component.html',
  styleUrls: ['./add-item.component.css']
})
export class AddItemComponent {
  sortCol: number;
  sortUp = false;
  searchStr: string;
  categories = ['JE (Civ)', 'JE (E/M)', 'JE(QS&C)', 'OS', 'AAO', 'UDC', 'SAA', 'LDC', 'JAA',
                                         'STENO GDE-III', 'STENO', 'DTMN', 'BS', 'SK', 'CHOW(O)'];
  logoPath = 'https://cecc-staff.github.io/assets/MES_Logo.png';
  item: Item = {
    MES_No: null,
    NAME: '',
    CATEGORY: '',
    DOB: '',
    DOJ: '',
    UNIT: '',
    UNIT_SENIORITY: '',
    STATION: '',
    STN_SENIORITY: '',
    REMARKS: ''
  };
  constructor(public itemService: ItemService) { }

  onSubmit() {
    this.item.MES_No = this.item.MES_No ? +this.item.MES_No : null;
    this.item.DOB = this.getDateStr(this.item.DOB);
    this.item.DOJ = this.getDateStr(this.item.DOJ);
    this.item.UNIT_SENIORITY = this.getDateStr(this.item.UNIT_SENIORITY);
    this.item.STN_SENIORITY = this.getDateStr(this.item.STN_SENIORITY);
    this.itemService.search.next(this.item);
    console.log(this.item);
    let str = '';
    if (this.item.MES_No) { str += 'MES No. = ' + this.item.MES_No; }
    str += this.item.MES_No ? ', ' : '';
    if (this.item.NAME) { str += 'Name = ' + this.item.NAME; }
    str += this.item.NAME ? ', ' : '';
    if (this.item.CATEGORY) { str += 'Category = ' + this.item.CATEGORY; }
    str += this.item.CATEGORY ? ', ' : '';
    if (this.item.DOB) { str += 'Date of Birth <= ' + this.item.DOB; }
    str += this.item.DOB ? ', ' : '';
    if (this.item.DOJ) { str += 'Date of Joining <= ' + this.item.DOJ; }
    str += this.item.DOJ ? ', ' : '';
    if (this.item.UNIT) { str += 'Unit = ' + this.item.UNIT; }
    str += this.item.UNIT ? ', ' : '';
    if (this.item.UNIT_SENIORITY) { str += 'Unit Seniority <= ' + this.item.UNIT_SENIORITY; }
    str += this.item.UNIT_SENIORITY ? ', ' : '';
    if (this.item.STATION) { str += 'Station = ' + this.item.STATION; }
    str += this.item.STATION ? ', ' : '';
    if (this.item.STN_SENIORITY) { str += 'Station Seniority <= ' + this.item.STN_SENIORITY; }
    str += this.item.STN_SENIORITY ? ', ' : '';
    if (this.item.REMARKS) { str += 'Remarks = ' + this.item.REMARKS; }
    this.searchStr = str;
    this.sortCol = -1;
  }
  getDateStr(dtStr: string) {
    if (dtStr) {
      const dt = new Date(dtStr);
      const month = dt.getMonth() + 1;
      const monthStr = month < 10 ? '0' + month : '' + month;
      const date = dt.getDate();
      const dateStr = date < 10 ? '0' + date : '' + date;
      return dt.getFullYear() + '-' + monthStr + '-' + dateStr;
    } else {
      return dtStr;
    }
  }
  onSort(index: number) {
    this.sortCol = index;
    this.sortUp = !this.sortUp;
    this.itemService.sort.next(index);
  }

}



