import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { FlexLayoutModule } from '@angular/flex-layout';
import { HttpModule } from '@angular/http';

import { AngularFireModule } from 'angularfire2';
import { AngularFirestoreModule } from 'angularfire2/firestore';
import { AngularFireStorageModule } from 'angularfire2/storage';
import { AngularFireAuthModule } from 'angularfire2/auth';

import { AppComponent } from './app.component';
import { environment } from '../environments/environment.prod';
import { ItemsComponent } from './components/items/items.component';
import { ItemService } from './services/item.service';
import { NavbarComponent } from './components/navbar/navbar.component';
import { AddItemComponent } from './components/add-item/add-item.component';
import { MaterialModule } from './material.module';
import { LoginComponent } from './login/login.component';
import { AppDateAdapter } from './services/AppDateAdapter';
import { DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE } from '@angular/material/core';
import { DeleteItemComponent } from './components/items/delete-item.component';
import { MAT_DIALOG_DEFAULT_OPTIONS } from '@angular/material';
import { PtoComponent } from './pto/pto.component';
import { PTOService } from './services/pto.service';
import { PtoListComponent } from './pto-list/pto-list.component';
import { PtoSearchComponent } from './pto-search/pto-search.component';
import { PtoNewComponent } from './pto-new/pto-new.component';
import { PtoPreviewComponent } from './pto-preview/pto-preview.component';
import { CasListComponent } from './cas-list/cas-list.component';
import { HelpComponent } from './help/help.component';
export const APP_DATE_FORMATS = {
    parse: {
        dateInput: { month: 'short', year: 'numeric', day: 'numeric' },
    },
    display: {
        dateInput: 'input',
        monthYearLabel: { year: 'numeric', month: 'numeric' },
        dateA11yLabel: { year: 'numeric', month: 'long', day: 'numeric' },
        monthYearA11yLabel: { year: 'numeric', month: 'long' },
    }
};
@NgModule({
  declarations: [
    AppComponent,
    ItemsComponent,
    NavbarComponent,
    AddItemComponent,
    LoginComponent,
    DeleteItemComponent,
    PtoComponent,
    PtoListComponent,
    PtoSearchComponent,
    PtoNewComponent,
    PtoPreviewComponent,
    CasListComponent,
    HelpComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    BrowserAnimationsModule,
    MaterialModule,
    ReactiveFormsModule,
    FlexLayoutModule,
    AngularFireModule.initializeApp(environment.firebase, 'cecc-staff'),
    AngularFirestoreModule,
    AngularFireStorageModule,
    AngularFireAuthModule,
    HttpModule
  ],
  providers: [ {provide: DateAdapter, useClass: AppDateAdapter},
               {provide: MAT_DATE_FORMATS, useValue: APP_DATE_FORMATS},
               {provide: MAT_DATE_LOCALE, useValue: 'en-GB'},
                ItemService, PTOService
  ],
  bootstrap: [AppComponent],
  entryComponents: [DeleteItemComponent]
})
export class AppModule { }



