import { Component } from '@angular/core';

@Component({
    selector: 'app-delete-item',
    template: ` <h2 mat-dialog-title>Are you sure to Delete ?</h2>
                <mat-dialog-content style="font-family:Arial,Helvetica,sans-serif;">
                    Record deleted once can't be undone.
                </mat-dialog-content>
                <mat-dialog-actions>
                    <button mat-raised-button color="warn" [mat-dialog-close]="true">Yes</button>
                    <button mat-raised-button color="primary" [mat-dialog-close]="false">No</button>
                </mat-dialog-actions> `
})
export class DeleteItemComponent {}



