import { Component, OnInit } from '@angular/core';
import { AngularFireAuth } from 'angularfire2/auth';
import { Http, Response , Headers} from '@angular/http';
import { ItemService } from '../../services/item.service';
import { Item } from '../../models/item';
import { Observable } from 'rxjs/Observable';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {
  showLoginCard = false;
  showPTO = true;
  // loggedIn = false;
  user = null;
  logStr = 'Login';
  ptoStr = '';
  headStr = '';
  items: Item[];
  url = 'https://us-central1-cecc-staff.cloudfunctions.net/addMessage';

  constructor(private afAuth: AngularFireAuth, private itemService: ItemService, private http: Http) {}

  ngOnInit() {
    this.afAuth.authState.subscribe(user => {
      if (user) {
        console.log('Logged in User : ' + JSON.stringify(user));
        this.showLoginCard = false;
        this.user = user;
        if (this.user.email) {
          this.logStr = 'Logout\n' + user.email;
          this.ptoStr = this.showPTO ? 'Database' : 'PTO';
          this.headStr = this.showPTO ? 'PTO' : 'Database';
        } else {
          this.logStr = 'Logout\n' + user.uid;
        }
      } else {
        this.user = null;
        this.logStr = 'Login';
        this.ptoStr = '';
        this.headStr = 'Database';
        this.showPTO = false;
      }
    });
    this.itemService.itemsSubject.subscribe((items: Item[]) => {
      this.items = items;
      this.setDownloadLink();
     });
  }
  setDownloadLink() {
    const itemsText = this.getItems();
    const data = new Blob([itemsText], {type: 'text/plain'});
    console.log('data : ' + data);
    const textFile = window.URL.createObjectURL(data);
    console.log('textFile : ' + textFile);
    (<HTMLAnchorElement>document.getElementById('downloadLink')).href = textFile;
  }

  login() {
    if (this.logStr === 'Login') {
      this.showLoginCard = true;
    } else {
      this.itemService.loginStatus.next();
      this.afAuth.auth.signOut();
    }
  }
  getItems() {
    const keys = ['MES_No', 'NAME', 'CATEGORY', 'DOB', 'DOJ', 'UNIT',
                      'UNIT_SENIORITY', 'STATION', 'STN_SENIORITY', 'REMARKS'];
    let str = keys.toString() + '\n';
    for (const item of this.items) {
        str += item.MES_No + ',';
        str += item.NAME + ',';
        str += item.CATEGORY + ',';
        str += item.DOB + ',';
        str += item.DOJ + ',';
        str += item.UNIT + ',';
        str += item.UNIT_SENIORITY + ',';
        str += item.STATION + ',';
        str += item.STN_SENIORITY + ',';
        str += item.REMARKS + '\n';
    }
    console.log('item.length : ' + this.items.length);
    return str;
  }
  publishMessage() {
      const headers = new Headers();
      headers.append('Content-Type', 'application/json');
      const message = { data: {text: 'namasteji'}};
      this.http.post(this.url, JSON.stringify(message), {headers: headers})
              .map((response: Response) => response.json())
              .catch((error: Response) => Observable.throw(error.json()))
              // tslint:disable-next-line:no-shadowed-variable
              .subscribe(result => {console.log(result); });
  }
  togglePTO() {
    this.showPTO = !this.showPTO;
    this.ptoStr = this.showPTO ? 'Database' : 'PTO';
    this.headStr = this.showPTO ? 'PTO' : 'Database';
  }
}



