import { MatListModule } from '@angular/material/list';
import { MatDialogModule } from '@angular/material/dialog';
import { MatSidenavModule } from '@angular/material/sidenav';

@NgModule({
    imports: [MatToolbarModule, MatCardModule, MatButtonModule, MatIconModule, MatListModule, MatSnackBarModule,
              MatInputModule, MatSelectModule, MatDatepickerModule, MatDialogModule, MatSidenavModule],
    exports: [MatToolbarModule, MatCardModule, MatButtonModule, MatIconModule, MatListModule, MatSnackBarModule,
              MatInputModule, MatSelectModule, MatDatepickerModule, MatDialogModule, MatSidenavModule]
})
export class MaterialModule {}



