import { Component, OnInit } from '@angular/core';

import { PTO } from '../models/pto';
import { PTOService } from '../services/pto.service';

@Component({
  selector: 'app-pto-search',
  templateUrl: './pto-search.component.html',
  styleUrls: ['./pto-search.component.css']
})
export class PtoSearchComponent implements OnInit {
  sortCol: number;
  sortUp: boolean;
  pto: PTO = {
    PTO_No: null,
    YEAR: null,
    UNIT: '',
    STATION: '',
    DATE: ''
  };
  constructor(public ptoService: PTOService) { }

  ngOnInit() {
    this.ptoService.search.subscribe((pto: PTO) => this.pto = pto);
  }

  onSubmit() {
    this.pto.PTO_No = this.pto.PTO_No ? +this.pto.PTO_No : null;
    this.pto.YEAR = this.pto.YEAR ? +this.pto.YEAR : null;
    this.pto.DATE = this.getDateStr(this.pto.DATE);

    this.ptoService.search.next(this.pto);
    console.log(this.pto);
    this.sortCol = -1;
  }
  getDateStr(dtStr: string) {
    if (dtStr) {
      const dt = new Date(dtStr);
      const month = dt.getMonth() + 1;
      const monthStr = month < 10 ? '0' + month : '' + month;
      const date = dt.getDate();
      const dateStr = date < 10 ? '0' + date : '' + date;
      return dt.getFullYear() + '-' + monthStr + '-' + dateStr;
    } else {
      return dtStr;
    }
  }
  onSort(index: number) {
    this.sortCol = index;
    this.sortUp = !this.sortUp;
    this.ptoService.sort.next(index);
  }

}



