import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument } from 'angularfire2/firestore';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';

import { Item } from '../models/item';
import { CollectionReference, Query } from '@firebase/firestore-types';

@Injectable()
export class ItemService {
  itemsCollection: AngularFirestoreCollection<Item>;
  items: Observable<Item[]>;
  itemDoc: AngularFirestoreDocument<Item>;
  search = new Subject();
  sort = new Subject();
  loginStatus = new Subject();
  itemsSubject = new Subject();
  count: number;

  constructor(public afs: AngularFirestore) {
    // this.items = this.afs.collection('items').valueChanges();
    this.itemsCollection = this.afs.collection('staff');
    this.items = this.itemsCollection.snapshotChanges().map(changes => {
      return changes.map(a => {
        const data = a.payload.doc.data() as Item;
        data.id = a.payload.doc.id;
        return data;
      });
    });
  }
  getItems() {
    console.log('getItems/ItemService Called, items.length = ' +
        this.items.map(a => a.forEach(item => console.log(item))));
    return this.items;
  }
  addItem(item: Item) {
    this.itemsCollection.add(item).then(function(docRef) {
        console.log('Document written with ID: ', docRef.id);
    });
  }
  deleteItem(item: Item) {
    this.itemDoc = this.afs.doc(`staff/${item.id}`);
    this.itemDoc.delete();
  }
  updateItem(item: Item) {
    console.log(item.id);
    this.itemDoc = this.afs.doc(`staff/${item.id}`);
    this.itemDoc.update(item);
  }
  filterItems(item: Item) {
    this.itemsCollection = this.afs.collection('staff',
        ref => {
          let query: CollectionReference | Query = ref;
          if (item.MES_No) { query = query.where('MES_No', '==', item.MES_No); }
          if (item.NAME) { query = query.where('NAME', '==', item.NAME); }
          if (item.CATEGORY) { query = query.where('CATEGORY', '==', item.CATEGORY); }
          if (item.DOB) { query = query.where('DOB', '<=', item.DOB); }
          if (item.DOJ) { query = query.where('DOJ', '<=', item.DOJ); }
          if (item.UNIT) { query = query.where('UNIT', '==', item.UNIT); }
          if (item.UNIT_SENIORITY) { query = query.where('UNIT_SENIORITY', '<=', item.UNIT_SENIORITY); }
          if (item.STATION) { query = query.where('STATION', '==', item.STATION); }
          if (item.STN_SENIORITY) { query = query.where('STN_SENIORITY', '<=', item.STN_SENIORITY); }
          if (item.REMARKS) { query = query.where('REMARKS', '==', item.REMARKS); }
          return query;
        }
    );
    return this.itemsCollection.snapshotChanges().map(changes => {
      return changes.map(a => {
        const data = a.payload.doc.data() as Item;
        data.id = a.payload.doc.id;
        return data;
      });
    });
  }

}




