import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormArray, AbstractControl } from '@angular/forms';
import { PTOService } from '../services/pto.service';

import { PTO } from '../models/pto';
import { Casuality } from '../models/casuality';
import { particulars } from '../models/particulars';

@Component({
  selector: 'app-pto-new',
  templateUrl: './pto-new.component.html',
  styleUrls: ['./pto-new.component.css']
})
export class PtoNewComponent implements OnInit {
  ptoForm: FormGroup;
  casForm: FormGroup;
  controls: AbstractControl[];

  categories = ['JE (Civ)', 'JE (E/M)', 'JE(QS&C)', 'OS', 'AAO', 'UDC', 'SAA', 'LDC', 'JAA',
                                         'STENO GDE-III', 'STENO', 'DTMN', 'BS', 'SK', 'CHOW(O)'];
  particulars = particulars;
  constructor(private ptoService: PTOService) { }

  ngOnInit() {
    this.ptoForm = new FormGroup({
      'ptono': new FormControl(this.ptoService.pto.PTO_No),
      'year': new FormControl(this.ptoService.pto.YEAR),
      'unit': new FormControl(this.ptoService.pto.UNIT),
      'station': new FormControl(this.ptoService.pto.STATION),
      'date': new FormControl(this.ptoService.pto.DATE),
      'casualities': new FormArray([]),
    });
    this.controls = (<FormArray>this.ptoForm.get('casualities')).controls;
    for (const cas of this.ptoService.pto.CASUALITIES) {
      this.addCasuality(cas);
    }
    this.ptoService.casuality.subscribe((casuality) => this.addCasuality(<Casuality>casuality));
    this.ptoService.preview.subscribe(() => this.preview());
  }
  addCasuality(cas: Casuality) {
    this.casForm = new FormGroup({
      'sno': new FormControl(cas.S_No),
      'mesno': new FormControl(cas.MES_No),
      'name': new FormControl(cas.NAME),
      'category': new FormControl(cas.CATEGORY),
      'date': new FormControl(cas.DATE),
      'type': new FormControl(cas.TYPE),
      'particular': new FormControl(cas.PARTICULAR)
    });
    (<FormArray>this.ptoForm.get('casualities')).push(this.casForm);
    // console.log(this.casuality);
    // this.page = 'casuality';
  }
  preview() {
    // console.log(this.ptoForm);
    this.ptoService.pto.PTO_No = +this.ptoForm.value.ptono;
    this.ptoService.pto.YEAR = +this.ptoForm.value.year;
    this.ptoService.pto.UNIT = this.ptoForm.value.unit;
    this.ptoService.pto.STATION = this.ptoForm.value.station;
    this.ptoService.pto.DATE = this.getDateStr(this.ptoForm.value.date),
    this.ptoService.pto.CASUALITIES = [];
    // this.addCasuality();
    for (const control of (<FormArray>this.ptoForm.get('casualities')).controls) {
      const casuality: Casuality = {
        S_No: 0,
        MES_No: 0,
        NAME: '',
        CATEGORY: '',
        DATE: '',
        PARTICULAR: ''
      };
      casuality.S_No = control.value.sno;
      casuality.MES_No = control.value.mesno;
      casuality.NAME = control.value.name;
      casuality.CATEGORY = control.value.category;
      casuality.DATE = this.getDateStr(control.value.date);
      casuality.TYPE = control.value.type;
      casuality.PARTICULAR = control.value.particular;
      this.ptoService.pto.CASUALITIES.push(casuality);
    }
    console.log(this.ptoService.pto);
  }
  getDateStr(dtStr: string) {
    if (dtStr) {
      const dt = new Date(dtStr);
      const month = dt.getMonth() + 1;
      const monthStr = month < 10 ? '0' + month : '' + month;
      const date = dt.getDate();
      const dateStr = date < 10 ? '0' + date : '' + date;
      return dt.getFullYear() + '-' + monthStr + '-' + dateStr;
    } else {
      return dtStr;
    }
  }
  onSelect(formControl: FormGroup, type: string) {
    formControl.patchValue({ particular: type});
  }
  deleteCas(formControl: FormGroup) {
    const casIndex = (<FormArray>this.ptoForm.get('casualities')).controls.indexOf(formControl);
    (<FormArray>this.ptoForm.get('casualities')).controls.splice(casIndex, 1);
    this.ptoService.pto.CASUALITIES.splice(casIndex, 1);
    console.log('After Delete-PTO : ', this.ptoService.pto);
  }

}



